


import msgTitle from '../../../components/title.vue'
import userName from '../../../components/userName.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import serviceGetData from '../../../service/getData.js'

export default {
  components:{
    msgTitle, userName, Checker, CheckerItem,Toast,
    Group,
    Checklist,
    Cell,
    Divider,
    XButton,
  },
  created(){

    try{
      // console.log(this.$store.state.applicant.default,1111)
      if(this.$store.state.applicant.default){ // 初始申请人存在
        this.cur_name = this.$store.state.applicant.default.firstName
        return
      }else{

        // 获取默认申请人
        serviceGetData.applicant_default_name({isSelf:true}).then(res=>{
          console.log(res.data[0])
          this.$store.state.applicant.default=res.data[0];
          this.cur_name=res.data[0].firstName
        },res=>console.log(res))

      }
    }catch(e){
      console.log('出错',e)
    }

  },
  mounted: function(){
    var vm=this;
    var start = async function () {
        var data=await serviceGetData.blocks('');
        vm.checkbox_val=[] // [data.data[0].blockId] // 默认值
        vm.blocks=data.data;
    };

    start();
  },
  methods: {
    change (val) {
      console.log('change', val)
    },
    next(){
      var checkbox_val = this.checkbox_val;
      if( checkbox_val.length == 0 ){
        this.show_Toast=true
        this.Toast_txt='请选择蜡块'
      }else{

        // 保存所选选择蜡块
          var blocks = this.blocks;
          console.log(blocks,222)

          if( blocks.length > 0 ){

            var arr=[]

            for(var i=0 ; i <= checkbox_val.length ; i++){
              for(var j=0; j <= blocks.length-1; j++){
                if( checkbox_val[i] == blocks[j].blockId){
                  arr.push(blocks[j])
                }
              }
            }

          }

          this.$store.commit('specialDye',[arr])


        // 带参数跳转
          this.$router.push({
                name: 'specialDye2',
                query: {
                  // routeParams: this.checkbox_val
                }
            })
      }

    },


    onNoError () {
      this.error = null
    },
    onError (errors) {
      console.log(errors)
      this.error = errors
    },

  },
  data () {
    return {
      cur_name:'',
      error: '',
      blocks: [], // 蜡块数据
      checkbox_val: [], // 蜡块默认值
      show_Toast: false,
      Toast_txt:'',
    }
  },
  computed:{
    checkbox_val_more(){}
  }
}




